@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  /* Light Theme Colors */
  --color-light-primary: #4f642b;
  --color-light-on-primary: #ffffff;
  --color-light-primary-container: #d2e9a5;
  --color-light-on-primary-container: #131f00;
  --color-light-surface: #fcfdf6;
  --color-light-on-surface: #1a1c18;
  --color-light-surface-variant: #e1e4d5;
  --color-light-on-surface-variant: #44483d;
  --color-light-outline: #75796c;
  --color-light-surface-container-low: #f6f7f0;
}

.dark {
  /* Dark Theme Colors */
  --color-dark-primary: #b6cc8b;
  --color-dark-on-primary: #233501;
  --color-dark-primary-container: #394c16;
  --color-dark-on-primary-container: #d2e9a5;
  --color-dark-surface: #1a1c18;
  --color-dark-on-surface: #e2e3dc;
  --color-dark-surface-variant: #44483d;
  --color-dark-on-surface-variant: #c5c8ba;
  --color-dark-outline: #8e9285;
  --color-dark-surface-container-low: #1e201c;
}

body {
  @apply bg-light-surface-container-low dark:bg-dark-surface-container-low text-light-on-surface dark:text-dark-on-surface transition-colors duration-300;
  font-family: 'Poppins', sans-serif;
}

